/**
 * ILoading
 * Version: 1.0.0
 * <i-loading></i-loading>
 */

import { Component, HostBinding, Input } from '@angular/core';

@Component({
  selector: 'i-loading',
  imports: [],
  template: `<div
      class="spinner-border spinner-border-sm"
      role="status"
      [class.light]="light"
    ></div>
    {{ label }}`,
})
export class ILoading {
  @Input() label = 'Loading..';

  @Input() light = false;

  @HostBinding('attr.light')
  get isLight(): boolean {
    return this.light;
  }
}
